import React from 'react';
import { createStore } from 'redux';

const store = createStore((state = {count : 0}, action) => {

  switch(action.type){
    case 'ADD' : return {count : state.count + 1};
      break ;
    case 'MULTIPLY' : return {count : state.count * 2};
      break ;
  }
});

console.log(store.getState());

store.dispatch({
  type: 'ADD'
});




